.App {
  height: 100%;
  display: flex;
  flex-direction: column;
  max-width: 500px;
  position: relative;
  margin: auto;
}

.cameraModal {
  height: 100%;
  position: absolute;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  z-index: 1;
  background-color: beige;
}

.cameraModal video {
  flex: 1;
}